import { useState } from 'react';
import { Button, Dialog, DialogTitle, DialogContent} from '@mui/material';
import '../App.css';

export default function Luukku12(props) {
    //dialog setOpen false or true
    const [open, setOpen] = useState(false);

    //opening a dialog
    const handleClickOpen = () => {
        setOpen(true);
    };

    //closing a dialog
    const handleClose = () => {
        setOpen(false);
    };
    
    if (props.date.get('date') < 12 || props.date.get('month') !== 11) {
        return (
            <p id="numeroharmaa">12</p>
        )
    } else {
        return (
        <>
            <Button onClick={handleClickOpen} variant='primary'><p id="numero">12</p></Button>
            <Dialog open={open} onClose={handleClose}>
                <DialogTitle><p id="otsikko">Luukku 12</p></DialogTitle>
                <DialogContent>
                    <iframe src="https://www.youtube.com/embed/x-fQBi45ieI" title="YouTube video player" frameBorder="0" 
                    allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowFullScreen></iframe>
                </DialogContent>
            </Dialog>
        </>
        )
    }
}