import { useState } from 'react';
import { Button, Dialog, DialogTitle, DialogContent} from '@mui/material';
import '../App.css';

export default function Luukku23(props) {
    //dialog setOpen false or true
    const [open, setOpen] = useState(false);

    //opening a dialog
    const handleClickOpen = () => {
        setOpen(true);
    };

    //closing a dialog
    const handleClose = () => {
        setOpen(false);
    };
    
    if (props.date.getDate() < 23 || props.date.getMonth() !== 11) {
        return (
            <p id="numeroharmaa">23</p>
        )
    } else {
        return (
        <>
            <Button onClick={handleClickOpen} variant='primary'><p id="numero">23</p></Button>
            <Dialog open={open} onClose={handleClose}>
                <DialogTitle><p id="otsikko">Luukku 23</p></DialogTitle>
                <DialogContent>
                    <img src="https://static.wikia.nocookie.net/muumitalo/images/0/07/Joulu_Muumiperhe_kuusi.jpg" alt="muumit joulukuusen alla"></img>
                </DialogContent>
            </Dialog>
        </>
        )
    }
}