import { useState } from 'react';
import { Button, Dialog, DialogTitle, DialogContent} from '@mui/material';
import '../App.css';

export default function Luukku4(props) {
    //dialog setOpen false or true
    const [open, setOpen] = useState(false);

    //opening a dialog
    const handleClickOpen = () => {
        setOpen(true);
    };

    //closing a dialog
    const handleClose = () => {
        setOpen(false);
    };
    
    if (props.date.get('date') < 4 || props.date.get('month') !== 11) {
        return (
            <p id="numeroharmaa">4</p>
        )
    } else {
        return (
        <>
            <Button onClick={handleClickOpen} variant='primary'><p id="numero">4</p></Button>
            <Dialog open={open} onClose={handleClose}>
                <DialogTitle><p id="otsikko">Luukku 4</p></DialogTitle>
                <DialogContent>
                    <p>Seikka on saletti.</p>
                </DialogContent>
            </Dialog>
        </>
        )
    }
}