import { useState } from 'react';
import { Button, Dialog, DialogTitle, DialogContent} from '@mui/material';
import '../App.css';

export default function Luukku13(props) {
    //dialog setOpen false or true
    const [open, setOpen] = useState(false);

    //opening a dialog
    const handleClickOpen = () => {
        setOpen(true);
    };

    //closing a dialog
    const handleClose = () => {
        setOpen(false);
    };
    
    if (props.date.getDate() < 13 || props.date.getMonth() !== 11) {
        return (
            <p id="numeroharmaa">13</p>
        )
    } else {
        return (
        <>
            <Button onClick={handleClickOpen} variant='primary'><p id="numero">13</p></Button>
            <Dialog open={open} onClose={handleClose}>
                <DialogTitle><p id="otsikko">Luukku 13</p></DialogTitle>
                <DialogContent>
                    <img src="https://upload.wikimedia.org/wikipedia/commons/0/07/Finlands_Lucia_Ingeborg_Spiik.jpg" alt="lucia"></img>
                </DialogContent>
            </Dialog>
        </>
        )
    }
}