import { useState } from 'react';
import { Button, Dialog, DialogTitle, DialogContent} from '@mui/material';
import '../App.css';
import dayjs from 'dayjs';

export default function Luukku10(props) {
    //dialog setOpen false or true
    const [open, setOpen] = useState(false);

    //opening a dialog
    const handleClickOpen = () => {
        setOpen(true);
    };

    //closing a dialog
    const handleClose = () => {
        setOpen(false);
    };
    
    if (dayjs().date() < 10 || dayjs().month() !== 11) {
        console.log("tämmäönn" + dayjs().date())
        return (
            <p id="numeroharmaa">10</p>
        )
    } else {
        console.log("tämmäönn" + dayjs().date())
        return (
        <>
            <Button onClick={handleClickOpen} variant='primary'><p id="numero">10</p></Button>
            <Dialog open={open} onClose={handleClose}>
                <DialogTitle><p id="otsikko">Luukku 10</p></DialogTitle>
                <DialogContent>
                    <p>miks sä veit sen veitsen?</p>
                </DialogContent>
            </Dialog>
        </>
        )
    }
}